<template>
  <section class="result" id="result" v-if="isResultShow">
    <HeadBar></HeadBar>
    <template v-if="isReaderSoso">
      <img src="../assets/TestOutput/soso.png" alt="">
      <div class="result__text">
        <h2>
          <div id="result__name"></div>
        </h2>
        <Share></Share>
        <h3></h3>
        <p></p>
        <!-- <button type="button" class="primary-btn"><a href="https://udn.com/vote2018/index" target="_blank"></a></button> -->
      </div>
    </template>

    <template v-else-if="isReaderBad">
      <img src="../assets/TestOutput/bad.png" alt="">
      <div class="result__text">
        <h2>
          <div id="result__name"></div>
        </h2>
        <!-- <Share></Share> -->
        <h3></h3>
        <p></p>
        <!-- <button type="button" class="primary-btn"><a href="https://udn.com/vote2018/index" target="_blank"></a></button> -->
      </div>
    </template>

    <template v-else>
      <img src="../assets/TestOutput/good.png" alt="">
      <div class="result__text">
        <h2>
          <div id="result__name"></div>
        </h2>
        <Share></Share>
        <h3></h3>
        <p></p>
        <!-- <button type="button" class="primary-btn"><a href="https://udn.com/vote2018/index" target="_blank"></a></button> -->
      </div>
    </template>
    <div class="result__share">
      <div class="result__share-fb">
        <p></p>
        <img src="../assets/Share/facebook.svg" alt="">
      </div>
      <div class="result__share-line">
        <p></p>
        <img src="../assets/Share/line.svg" alt="">
      </div>
      <div class="result__share-election">
        <a href="https://udn.com/vote2018/index" target="_blank">
          <p></p>
        </a>
      </div>
    </div>

  </section>
</template>

<script>
import {
  TweenLite, Power2,
} from 'gsap/TweenMax';
import HeadBar from './HeadBar.vue';
// import Share from './Share.vue';

export default {
  name: 'Result',
  components: {
    HeadBar,
    // Share,
  },
  data() {
    return {
      isResultShow: false,
      // isResultShow: true,
      isReaderSoso: true,
      isReaderBad: true,
      resultNames: ['', '', ''],
      typeNameTimer: null,
    };
  },
  methods: {
    resultSlideInDynamic() {
      TweenLite.to('#result', 0.3, {
        opacity: 1,
        ease: Power2.easeIn,
        onComplete: () => {
          if (this.isReaderSoso) {
            this.typeResultName(0);
          } else if (this.isReaderBad) {
            this.typeResultName(1);
          } else {
            this.typeResultName(2);
          }
        },
      });
    },
    typeResultName(resultNameIdx) {
      const ResultName = document.getElementById('result__name');
      const splitResultName = this.resultNames[resultNameIdx].split('');
      let resultNameStr = '';
      let idx = 0;
      const typeName = () => {
        this.typeNameTimer = setTimeout(() => {
          resultNameStr += splitResultName[idx];
          ResultName.textContent = resultNameStr;
          idx += 1;
          if (idx !== splitResultName.length) {
            typeName();
          } else {
            clearTimeout(this.typeNameTimer);
          }
        }, 160);
      };
      typeName();
    },
  },
};
</script>

<style lang="scss">
.result {
  opacity: 0;
  flex: 0 0 100%;
  box-sizing: border-box;
  position: relative;
  &__share {
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    justify-content: space-between;
    padding-top: 4px;
    padding-bottom: 45px;
    &-fb {
      background-color: #3b62b8;
      & p {
        margin-right: 6px;
      }
    }
    &-line {
      background-color: #00c329;
      & p {
        margin-right: 6px;
      }
    }
    &-election {
      background-color: #d14033;
      &:hover {
        opacity: 0.6;
        background-color: #c64033;
      }
    }
    & p {
      display: inline-block;
      font-size: 1.5rem;
      color: #fff;
    }
    & img {
      width: 20px;
      height: auto;
    }
    & > div {
      width: 31%;
      border-radius: 100px;
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: all 0.3s;
      &:hover {
        opacity: 0.6;
      }
    }
  }
  & > img {
    width: 100%;
    height: auto;
  }
  & h2 {
    width: 100%;
    // position: relative;
    height: 50px;
    border: 0.5px solid #707070;
    font-size: 2.8rem;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px;
    box-sizing: border-box;
    background-color: #fff;
    // margin-bottom: 22px;
    // margin-bottom: 10px;
    margin-bottom: 20px;
    & > div {
      width: 100%;
      border: 0.5px solid #707070;
      height: 42px;
      box-sizing: border-box;
      line-height: 42px;
      text-align: center;
    }
  }
  & h3 {
    // font-size: 2rem;
    line-height: 1.35;
    font-weight: 700;
    margin-bottom: 5px;
  }
  &__text {
    padding-right: 20px;
    padding-left: 20px;
    font-size: 2rem;
    transform: translateY(-30px);
    & p {
      line-height: 1.25;
      // margin-bottom: 30px;
    }
  }
  & a {
    color: #fff;
    display: block;
  }
}
</style>
